.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
}

.profile__avatar:hover {
  opacity: 0.5;
}

@media (max-width: 1023px) {
  .profile__avatar {
    margin: 0 0 0 27px;
  }
}

@media (max-width: 767px) {
  .profile__avatar {
    margin: 0 auto;
  }
}
